






































import Vue from 'vue'

interface Generics {
  [key: string] : any
}

export default Vue.extend({
  data () {
    return {
      username: '',
      usernameValid: true,
      room: '',
      roomValid: true,
      loading: false,
      errorMessages: {
        username: 'Username should not be empty',
        room: 'RoomID should not be empty'
      }
    }
  },
  watch: {
    username (): void {
      this.usernameValid = this.username !== ''
    },
    room (): void {
      this.roomValid = this.room !== ''
    }
  },
  computed: {
    socket () {
      return this.$store.state.socket
    }
  },
  methods: {
    async login (e: Event): Promise<void> {
      e.preventDefault()

      if (this.validateInput()) {
        this.loading = true

        this.$store.dispatch('doLogin', { username: this.username, room: this.room })
          .then(({ data }: Generics) => {
            localStorage.setItem('user_id', data._id)
            this.$store.commit('SET_CURRENT_USER', data)

            this.$router.push('/')
            this.clearInputs()
          })
          .catch(({ response }: Generics) => {
            this.usernameValid = false
            this.errorMessages.username = response.data.message
          })
          .finally((): void => {
            this.loading = false
          })
      }
    },
    clearInputs (): void {
      this.username = ''
      this.usernameValid = true
      this.room = ''
      this.roomValid = true
      this.errorMessages = {
        username: 'Username should not be empty',
        room: 'RoomID should not be empty'
      }
    },
    validateInput (): boolean {
      this.usernameValid = (this.username !== '')
      this.roomValid = (this.room !== '')

      return this.usernameValid && this.roomValid
    }
  }
})
