



































import Vue from 'vue'

interface Chat {
  [key: string]: string
}

interface LocalState {
  text: string
}

export default Vue.extend({
  data (): LocalState {
    return {
      text: ''
    }
  },
  computed: {
    chats (): Array<Chat> {
      return this.$store.state.chats
    },
    currentUser (): { [key: string]: string } {
      return this.$store.state.currentUser
    },
    socket () {
      return this.$store.state.socket
    }
  },
  methods: {
    sendMessage (e: Event): void {
      e.preventDefault()

      if (this.text !== '') {
        const payload = {
          text: this.text,
          sender_id: this.currentUser._id,
          sender: this.currentUser.username,
          room: this.currentUser.room
        }

        this.$store.dispatch('sendMessage', payload)
          .then(({ data }) => {
            this.socket.emit('send message', data)
            this.$store.commit('SET_CHATS', [...this.chats, data])
            this.text = ''

            this.scrollDown()
          })
          .catch((err: Error) => {
            console.log(err)
          })
      }
    },
    scrollDown (): void {
      const chatContainer = this.$el.querySelector('#chat-container')

      if (chatContainer) {
        setTimeout(() => {
          chatContainer.scrollTop = chatContainer.scrollHeight
        }, 500)
      }
    }
  },
  mounted (): void {
    this.scrollDown()

    this.socket.on('other sent message', (chat: Chat) => {
      if (chat.room === this.currentUser.room) {
        this.$store.commit('SET_CHATS', [...this.chats, chat])
        this.scrollDown()
      }
    })
  }
})
